<template>
      <tr class="discount-list-item"
        @click="editDiscount">
            <td>{{name}}</td>
            <td>{{typeName}}</td>
            <td class="hidden-xs-only">{{dateFormat(dateFrom)}}</td>
            <td class="hidden-xs-only">{{dateFormat(dateTo)}}</td>
            <td v-if="status" 
                class="green--text">
                {{LABELS.statusLabelActive}}
            </td>
            <td v-if="!status" 
                class="red--text">
                {{LABELS.statusLabelInactive}}
            </td>
          <td>
            <v-tooltip top>
                <template v-slot:activator="{ on, attrs }">
                    <i class="material-icons red--text"
                        @click.stop="removingDialog = true"
                        v-bind="attrs"
                        v-on="on"
                        >delete
                    </i>
                </template>
                <span>{{LABELS.discountListRemove}}</span>
            </v-tooltip>
            <v-dialog
                v-model="removingDialog"
                max-width="290">
                <v-card>
                    <v-card-title 
                        class="headline">
                        {{LABELS.removingHeader}}
                    </v-card-title>
                    <v-card-text>
                        {{LABELS.removingText}}
                    </v-card-text>
                    <v-card-actions>
                        <v-btn
                            color="green darken-1"
                            text
                            @click="removeDiscount"
                        >
                            {{LABELS.discountListRemove}}
                        </v-btn>

                        <v-btn
                            color="green darken-1"
                            text
                            @click="removingDialog = false"
                        >
                        {{LABELS.closeButton}}
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
          </td>
      </tr>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
    name: 'w-discount-list-item',
    data() {
        return{
            removingDialog: false,
        }
    },
    props: {
        name: {
            type: String,
            default: '',
        },
        type: {
            type: Object,
            default() {},
        },
        dateFrom: {
            type: String,
            default: '',
        },
        dateTo: {
            type: String,
            default: '',
        },
    },
    computed: {
        ...mapGetters([
            'LABELS',
            'DISCOUNT_TYPES',
        ]),
        status(){
            let date = new Date().toISOString();
            let divider = date.indexOf('T');
            let today = date.slice(0, divider);
            let status;
            if (this.dateFrom <= today && this.dateTo >= today){
                status = 'active';
            } else if (this.dateFrom == null || this.dateTo == null){
                status = 'active';
            }
            return status;
        },
        typeName(){
            let type = this.DISCOUNT_TYPES.find(type => type.id == this.type.id);
            return type.name;
        }
    },
    methods: {
        removeDiscount(){
            this.$emit('removeDiscount');
            this.removingDialog = false;
        },
        editDiscount(){
            this.$emit('editDiscount');
        },
        dateFormat(date){
            if (date){
                return date.split('-').reverse().join('.');
            } else {
                return '';
            }
        },
    }
}
</script>

<style scoped>
    .discount-list-item{
        cursor: pointer;
    }
</style>