<template>
  <div class="w-gift-list">
      <v-card>
          <v-subheader>{{LABELS.giftListHeader}}</v-subheader>
      <v-list dense>
          <v-list-item class="no-flex"
            v-for="(item, index) in PRODUCT_LIST" 
            :key="item.id">
            <span>{{item.name}}</span>
            <i class="material-icons remove-gift"
                @click="removeGiftItem(index)">
                close
            </i>
          </v-list-item>
      </v-list>
      </v-card>
  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
export default {
    name: 'w-gift-list',
    computed: {
        ...mapGetters([
            'LABELS',
            'PRODUCT_LIST'
        ])
    },
    watch: {
        PRODUCT_LIST(){
            if (this.PRODUCT_LIST.length > 1){
                this.removeGiftItem(0)
            }
        }
    },
    methods: {
        ...mapActions([
            'REMOVE_PRODUCT_FROM_LIST'
        ]),
        removeGiftItem(index){
            this.REMOVE_PRODUCT_FROM_LIST(index);
        }
    }
}
</script>

<style scoped>
    .no-flex{
        display: block;
    }
    .remove-gift{
        float: right;
        cursor: pointer;
    }
</style>