import axios from 'axios'
const url = window.location.origin + '/wp-content/plugins/woo-all-in-one-discounts-and-gifts/includes/ajax-api/';

export default {
    GET_LABELS({commit}){
        return axios.post(url + 'send-headings.php', {
            
        })
        .then(data => {
            console.log(data)
            commit('SET_LABELS', data.data.labels)
            commit('SET_TYPES', data.data.types)
            return data})
        .catch(error => {
            console.log('Error: ' + error);
        })
    },
    GET_PRODUCTS_FROM_API({commit}){
        return axios.post(url + 'send-products.php', {
            
        })
        .then(data => {
            commit('SET_PRODUCTS_TO_STATE', data.data)
            return data})
        .catch(error => {
            console.log('Error: ' + error);
        })
    },

    GET_DISCOUNTS_FROM_API({commit}){

       return axios.post(url + 'send-discounts.php', {
            
        })
        .then(data => {
            console.log(data)
            commit('SET_DISCOUNTS_TO_STATE', data.data)
            return data})
        .catch(error => {
            console.log('Error: ' + error);
        })
    },
    ADD_PRODUCT_TO_LIST({commit}, product){
        commit('ADD_PRODUCT_TO_LIST', product)
    },
    REMOVE_PRODUCT_FROM_LIST({commit}, index){
        commit('REMOVE_PRODUCT_FROM_LIST', index);
    },
    CLEAR_PRODUCT_LIST({commit}){
        commit('CLEAR_PRODUCT_LIST');
    },
    REMOVE_DISCOUNT({commit}, data){
        axios.post(url + 'remove-discount.php',{
            data: data.id
        })
        .then(response => {
            let res = response.data;
            console.log(res);
        })
        .then(commit('REMOVE_DISCOUNT', data.index))
        .catch(error => console.log('Error: ' + error))
    }, 
    SAVE_DISCOUNT({commit}, discount){
        axios.post(url + 'save-discount.php', {
            data: discount,
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            }
        })
        .then(response => {
            let res = response.data;
            console.log(res);
        })
        .then(commit('SAVE_DISCOUNT', discount))
        .catch(error => console.log('Error: ' + error))
    },
    EDIT_ON_SERVER(discount){
        console.log(discount);
        axios.post(url + 'save-discount.php', {
            data: discount,
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            }
        })
        .then(response => {
            let res = response.data;
            console.log(res);
        })
        .catch(error => console.log('Error: ' + error))
    },
    SET_DESCRIPTION({commit}, description){
        commit('SET_DESCRIPTION', description);
    },
    SET_CAT_DESCRIPTION({commit}, description){
        commit('SET_CAT_DESCRIPTION', description);
    },
    CLEAR_DESCRIPTION({commit}){
        commit('CLEAR_DESCRIPTION');
    }
}