<template>
  <div class="w-product-list height-100 width-100">
      <v-row class="height-100">

            <v-card
                height="100%"
                width="100%"
                outlined
                class="product-list px-4 py-4"
                elevation="1">
                <w-chip
                    v-for="(item, index) in PRODUCT_LIST"
                    :key="item.id"
                    :name="item.id"
                    @remove="removeSelected(index)"
                    :class="color(item.itemType)"/>
            </v-card>

      </v-row>
  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
import wChip from './w-chip.vue'
export default {
    name: 'w-product-list',
    components: {
        wChip
    },
    computed: {
        ...mapGetters([
            'LABELS',
            'PRODUCTS',
            'PRODUCT_LIST',
        ]),
        color(){
            return (type) => {
                return {
                'green green--text': type == 'product',
                'orange orange--text': type == 'category',
                'purple purple--text': type == 'tag',
                }
            }
        },
    },
    methods: {
        ...mapActions([
            'REMOVE_PRODUCT_FROM_LIST'
        ]),
        removeSelected(index){
            this.REMOVE_PRODUCT_FROM_LIST(index);
        }
    }


}
</script>

<style scoped>
    .height-100{
        height: 100%;
    }
    .width-100{
        width: 100%;
    }
</style>