<template>
          <div class="w-search-product">
              <v-row>
                  <v-col>
                    <v-autocomplete 
                        :no-data-text="LABELS.typeProductName"
                        :label="LABELS.productListSearchProduct"
                        @change="addProductToList"
                        v-model="selectedProductId"
                        :search-input.sync="search"
                        :items="filteredProducts"
                        append-icon="search"
                        item-text="name"
                        item-value="id">
                    </v-autocomplete>
                  </v-col>
              </v-row>
        </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'

export default {
    name: 'w-search-product',
    data(){
        return{
            search: null,
            filteredProducts: [],
            productList: [],
            selectedProductId: null,
        }
    },
    computed: {
        ...mapGetters([
            'LABELS',
            'PRODUCTS',
            'PRODUCT_LIST',
        ])
    },
    watch: {
        search(val){
            if (val){
                this.searchProducts();
            }
        }
    },
    methods: {
        ...mapActions([
            'ADD_PRODUCT_TO_LIST'
        ]),
        searchProducts(){
            this.filteredProducts = this.PRODUCTS.filter(item => {
                return item.name.toLowerCase().includes(this.search);
            })
        },
        addProductToList(){
            if (this.selectedProductId){
                let selectedProduct = this.PRODUCTS.find(product => product.id == this.selectedProductId);
                selectedProduct.itemType = 'product';
                if (!this.PRODUCT_LIST.includes(selectedProduct)){
                    this.ADD_PRODUCT_TO_LIST(selectedProduct);
                    this.selectedProductId = null;
                }
            }
        },
    }
}
</script>

