export default {
    SET_LABELS: (state, labels) => {
        state.labels = labels;
    },
    SET_TYPES: (state, types) => {
        state.discountTypes = types;
    },
    SET_PRODUCTS_TO_STATE: (state, products) => {
        state.products = products;
    },
    SET_DISCOUNTS_TO_STATE: (state, discounts) => {
        state.discounts = discounts;
    },
    REMOVE_DISCOUNT: (state, index) => {
        state.discounts.splice(index, 1);
    },
    SAVE_DISCOUNT: (state, discount) => {
        let existingDiscount = state.discounts.find(item => item.id == discount.id);
        if (!existingDiscount){
            state.discounts.push(discount);
        }
    },
    ADD_PRODUCT_TO_LIST: (state, product) => {
        state.productList.push(product);
    },
    REMOVE_PRODUCT_FROM_LIST: (state, index) => {
        state.productList.splice(index, 1);
    },
    CLEAR_PRODUCT_LIST: (state) => {
        state.productList = [];
    },
    SET_DESCRIPTION: (state, description) => { 
        state.description.productDescription = description;
    },
    SET_CAT_DESCRIPTION: (state, description) => {
        state.description.catDescription = description;
    },
    CLEAR_DESCRIPTION: (state) => {
        state.description.productDescription = null;
        state.description.catDescription = null;
    }
}