<template>
  <div class="w-range-datepicker">
    <v-menu
        ref="menu"
        v-model="menu"
        :close-on-content-click="false"
        :return-value.sync="dates"
        transition="scale-transition"
        offset-y
        min-width="290px">
            <template v-slot:activator="{ on, attrs }">
                <v-text-field
                    clearable
                    v-model="dateRangeText"
                    :label="LABELS.editDiscountDates"
                    append-icon="event"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                ></v-text-field>
                </template>
                <v-date-picker 
                    :range="true"
                    first-day-of-week="1"
                    color="success"
                    v-model="dates">
                    <v-spacer></v-spacer>
                    <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
                    <v-btn text color="primary" @click="$refs.menu.save(dates)">OK</v-btn>
                </v-date-picker> 
            </v-menu>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
    name: 'w-range-datepicker',
    props: {
        initialDates: {
            type: Array,
            default() {[]},
        }
    },
    
    data(){
        return{
            dates: [],
            formattedDates: [],
            menu: false,
        }
    },
    computed: {
        ...mapGetters([
        'LABELS',
        ]),
        dateRangeText () {
            let dateRange;
            if (this.dates[0] !== ''){
                dateRange = this.formattedDates.join(' ~ ')
            } else {
                dateRange = null;
            }
            return dateRange;
        },
    },
    mounted(){
        this.dates = this.initialDates;
    },
    watch: {
        dates(){
            if (this.dates[0]){
                this.formattedDates = this.dateFormat(this.dates);
                this.$emit('sendDates', this.dates);
            }
        }
    },
    methods: {
        dateFormat(values){
        let formattedDates = [];
            for (let value of values){
                let formattedDate = value.split('-').reverse().join('.');
                formattedDates.push(formattedDate);
            }
            return formattedDates.sort();
        }
    }
}
</script>

<style>
    .w-range-datepicker{
        width: 100%;
    }
</style>