<template>
    <div class="w-descriptions mt-n3">
        <v-row>
            <v-col cols="6">
                <div class="editor-elem">
                    <v-text-field
                        :placeholder="LABELS.productDescriptionPlaceholder"
                        v-model="productDescription"/>
                    <w-tooltip>{{LABELS.infoProductDescription}}</w-tooltip>
                </div>
            </v-col>
            <v-col cols="6">
                <div class="editor-elem">
                    <v-text-field
                        :placeholder="LABELS.productCatDescriptionPlaceholder"
                        v-model="catDescription"/>
                    <w-tooltip>{{LABELS.infoCatDescription}}</w-tooltip>
                </div>
            </v-col>
        </v-row>
    </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
import wTooltip from './w-tooltip'
export default {
    name: 'w-descriptions',
    components: {
        wTooltip
    },
    computed: {
        ...mapGetters([
            'DESCRIPTION',
            'LABELS'
        ]),
        productDescription: { 
            set(val){ 
                this.SET_DESCRIPTION(val);
            },
            get(){
                return this.DESCRIPTION.productDescription;
            }
        },
        catDescription: { 
            set(val){
                this.SET_CAT_DESCRIPTION(val);
            },
            get(){
                return this.DESCRIPTION.catDescription;
            }
        }

    },
    methods: {
        ...mapActions([
            'SET_DESCRIPTION',
            'SET_CAT_DESCRIPTION'
        ])
    }
}
</script>

<style scoped>
    .w-descriptions{
        width: 100%;
    }
    .editor-elem{
        display: flex;
    }
</style>