<template>
<div class="w-tooltip">
    <v-btn
        @click="tooltipToggle"
        icon>
        <v-icon>mdi-help-circle</v-icon>
    </v-btn>
        <v-snackbar
            absolute
            centered
            @click="tooltipToggle"
            v-model="tooltip"
            :timeout="-1"
            color="success"
            outlined
            >
           <slot></slot>
           <v-btn 
                class="tooltip-button"
                @click="tooltipToggle"
                text 
                color="success">
                {{LABELS.closeButton}}
            </v-btn>
        </v-snackbar>
    </div>
</template>

<script>
import {mapGetters} from 'vuex'
export default {

    name: 'w-tooltip',
    computed: {
        ...mapGetters([
            'LABELS'
        ])
    },
    data(){
        return{
            tooltip: false,
        }
    },
    methods: {
        tooltipToggle(){
            this.tooltip = !this.tooltip;
        }
    }
}
</script>

<style scoped>
    .tooltip-button{
        float: right;
    }
</style>