<template>
    <v-chip
      class="mx-1 my-1"
      outlined
      close
      @click:close="remove"
      >{{itemName}}
    </v-chip>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
  name: 'w-chip',
  props: {
    name
  },
  computed: {
    ...mapGetters([
        'PRODUCTS',
    ]),
    itemName(){
      let product = this.PRODUCTS.find(product => product.id == this.name);
      return product.name;
    }
  },
  methods: {
    remove(){
      this.$emit('remove');
    }
  }
}
</script>

