<template>
    <v-row>
        <v-col cols="6" class="py-0">
            <v-text-field
                :rules="[rules.required]"
                type="number"
                :error-messages="errorMessage"
                v-model="from"
                :label="LABELS.subtotalFromPlaceholder"
            />
        </v-col>
        <v-col cols="6" class="py-0">
            <v-text-field
                :rules="[rules.required]"
                type="number"
                v-model="to"
                :label="LABELS.subtotalToPlaceholder"
            />
        </v-col>
        
    </v-row>
</template>

<script>
import {mapGetters} from 'vuex'
import {required} from 'vuelidate/lib/validators'
export default {
    name: 'w-subtotal',
    props: ['rules', 'subtotalTo', 'subtotalFrom', 'error'],
    data(){
        return{
            to: this.subtotalTo,
            from: this.subtotalFrom,
        }
    },
    validations: {
        to: {required}
    },
    computed: {
        ...mapGetters([
            'LABELS'
        ]),
        errorMessage(){
            if (this.error){
                return 'error'
            } else {
                return []
            }
        }

    },

    watch: {
        from(){
            this.$emit('subtotalFrom', this.from);
        },
        to(){
            this.$emit('subtotalTo', this.to);
        }
    }
}
</script>

