<template>
   <v-card class="w-discount-editor">
       <v-card-title class="justify-center success white--text" color="success">
           {{LABELS.editDiscountHeader}}
           <v-spacer></v-spacer>
            <v-btn icon dark @click="hideEditor">
                <v-icon>mdi-close</v-icon>
            </v-btn>
       </v-card-title>
       <v-card-text>
           <v-container class="px-0">
           <h3>{{LABELS.commonBlockHeader}}</h3>
           
           <v-row>
               <v-col cols="12" sm="4">
                   <div class="editor-elem">
                        <v-text-field
                            :label="LABELS.editDiscountNameInput"
                            v-model="discount.name"
                            :rules="[rules.required]"
                            :error-messages="nameErrors"/>
                        <w-tooltip>{{LABELS.infoDiscountName}}</w-tooltip>
                    </div>
               </v-col>
                <v-col cols="12" sm="4">
                    <div class="editor-elem">
                        <v-select
                            v-model="discount.type.id"
                            :items="TYPES"
                            :error-messages="typeErrors"
                            :label="LABELS.discountListType"
                            item-text="name"
                            item-value="id"
                            @change="CLEAR_PRODUCT_LIST"
                            >
                        </v-select>
                        <w-tooltip>{{LABELS.infoDiscountType}}</w-tooltip>
                    </div>
                </v-col>
                <v-col cols="12" sm="4">
                    <div class="editor-elem">
                        <w-range-datepicker
                        @sendDates="receiveDates"
                        :initialDates="[discount.dateFrom, discount.dateTo]"/>
                        <w-tooltip>{{LABELS.infoDiscountDates}}</w-tooltip>
                    </div>
                </v-col>
            </v-row>
            </v-container>
            <v-container class="px-0" v-if="discount.type.id.length && discount.type.id == 'common'">
                <h3>{{LABELS.subtotalBlockHeader}}</h3>
                <v-row>
                    <v-col cols="12" sm="4">
                        <div class="editor-elem">
                            <w-subtotal 
                                :rules="rules"
                                :subtotalFrom="discount.properties.subtotalFrom"
                                :subtotalTo="discount.properties.subtotalTo"
                                @subtotalFrom="subtotalFrom"
                                @subtotalTo="subtotalTo"/>
                            <w-tooltip>{{LABELS.infoSubtotal}}</w-tooltip>
                        </div>
                    </v-col>
                <v-col cols="12" sm="4">
                    <div class="editor-elem">
                        <v-text-field
                            :error-messages="discountErrors"
                            :rules="[rules.required, rules.regexp]"
                            v-model="discount.properties.discount"
                            :label="LABELS.commonDiscountPlaceholder">
                        </v-text-field>
                        <w-tooltip>{{LABELS.infoCommonDiscount}}</w-tooltip>
                    </div>
                </v-col>
                <v-col cols="12" sm="4">
                    <div class="editor-elem">
                        <v-text-field
                            v-model="discount.properties.cartString"
                            :label="LABELS.commonCartStringPlaceholder">
                        </v-text-field>
                        <w-tooltip>{{LABELS.infoCartString}}</w-tooltip>
                    </div>
                </v-col>
            </v-row>
                </v-container>
            <v-container 
                class="px-0" 
                v-if="discount.type.id.length && discount.type.id != 'common' && discount.type.id != 'gift'">
                <div class="wrapper d-flex">
                    <h3 class="pt-3"
                        >{{LABELS.productListBlockHeader}}
                    </h3>
                    <w-tooltip>{{LABELS.infoProductList}}</w-tooltip>
                </div>
                
                <v-row>
                    
                <v-col cols="12" sm="4" class="flex-column">
                    <w-select-taxonomy/>
                    <w-search-product/>
                </v-col>
                <v-col cols="12" sm="8">
                    <w-product-list/>
                </v-col>
            </v-row>
            </v-container>
            <v-container class="px-0" v-if="discount.type.id == 'products'">
                <h3>{{LABELS.productBlockHeader}}</h3>
                            <v-row>
                <v-col cols="12" sm="4">
                    <div class="editor-elem">
                        <v-text-field 
                            v-model="discount.properties.discount"
                            :placeholder="LABELS.productDiscountPlaceholder"
                            :rules="[rules.required, rules.regexp]"
                        ></v-text-field>
                        <w-tooltip>{{LABELS.infoCommonDiscount}}</w-tooltip>
                    </div>
                </v-col>
                 <v-col cols="12" sm="8">
                    <w-descriptions />
                </v-col>
            </v-row>
            </v-container>
            <v-container class="px-0" v-if="discount.type.id == 'payTake'">
                <h3>{{LABELS.payTakeBlockHeader}}</h3>
            <v-row>
                <v-col cols="6" sm="2">
                    <div class="editor-elem">
                        <v-text-field
                            :rules="[rules.required, rules.regexp]"
                            :placeholder="LABELS.payTakeItemsToPay"
                            v-model="discount.properties.itemsPay"/>
                        <w-tooltip>{{LABELS.infoItemsPay}}</w-tooltip>
                    </div>
                </v-col>
                <v-col cols="6" sm="2">
                    <div class="editor-elem">
                        <v-text-field
                            :rules="[rules.required, rules.regexp]"
                            :placeholder="LABELS.payTakeItemsToTake"
                            v-model="discount.properties.itemsTake"/>
                        <w-tooltip>{{LABELS.infoItemsTake}}</w-tooltip>
                    </div>
                </v-col>
                 <v-col cols="12" sm="8">
                    <w-descriptions />
                </v-col>
            </v-row>
            </v-container>
            <v-container class="px-0" v-if="discount.type.id == 'gift'">
                <h3>{{LABELS.giftBlockHeader}}</h3>
                            <v-row>
                <v-col cols="12" sm="4">
                    <div class="editor-elem">
                        <w-subtotal 
                            :error="subtotalErrors"
                            :rules="rules"
                            @subtotalFrom="subtotalFrom"
                            @subtotalTo="subtotalTo"
                            :subtotalFrom="discount.properties.subtotalFrom"
                            :subtotalTo="discount.properties.subtotalTo"/>
                        <w-tooltip>{{LABELS.infoSubtotal}}</w-tooltip>
                    </div>
                </v-col>
                <v-col cols="12" sm="4">
                    <div class="editor-elem">
                        <w-search-product
                            class="mt-n3"
                        />
                        <w-tooltip>{{LABELS.infoGiftProduct}}</w-tooltip>
                    </div>
                </v-col>
                <v-col cols="12" sm="4">
                    <w-gift-list />
                </v-col>
            </v-row>
            </v-container>
       </v-card-text>
       <v-card-actions class="justify-center">
           <v-btn 
                color="success"
                :disabled="!isValid"
                @click="saveDiscount"
                >{{LABELS.saveButton}}
           </v-btn>
           <v-btn
                @click="hideEditor">
               {{LABELS.closeButton}}
           </v-btn>
       </v-card-actions>
           <v-alert v-model="alert"
                type="error"
                dismissible>
                {{alertContent}}
            </v-alert>
   </v-card>
</template>

<script>
import { validationMixin } from 'vuelidate'
import {required} from 'vuelidate/lib/validators'
import {mapGetters, mapActions} from 'vuex'
import wSubtotal from './w-subtotal'
import wRangeDatepicker from './w-range-datepicker'
import wProductList from './w-product-list'
import wSearchProduct from './w-search-product'
import wSelectTaxonomy from './w-select-taxonomy'
import wGiftList from './w-gift-list'
import wDescriptions from './w-descriptions'
import wTooltip from './w-tooltip'
export default {
    mixins: [validationMixin],
    name: 'w-discount-editor',
    components: {
        wDescriptions,
        wRangeDatepicker,
        wProductList,
        wSearchProduct,
        wSelectTaxonomy,
        wGiftList,
        wSubtotal,
        wTooltip,
    },
    data(){
        return{
            rules: {
                required: value => !!value || 'Required.',
                /* eslint-disable */
                regexp: value => {
                    const pattern = /^-?([0-9?,.]+(\.[0-9]{2})?\%?)$/
                    return pattern.test(value) || 'Invalid' }
                /* eslint-enable */
            },
            discount: 
            {   
                id: null,
                name: '',
                type: {
                    id: '',
                },
                dateFrom: null,
                dateTo: null,
                products: [],
                properties: {
                    subtotalFrom: null,
                    subtotalTo: null,
                    discount: null,
                    cartString: null,
                    productDescription: '',
                    catDescription: '',
                    itemsPay: null,
                    itemsTake: null,

                },
            },
            alert: false,
            alertContent: ''
        }
    },
    validations() {
        if (this.discount.type.id == 'common' || this.discount.type.id == 'product'){
            return {
                discount: {
                name: {required},
                type: {
                    id: {required}
                    },
                properties: {
                    discount: {required}
                    }
                },
            }
        } else if (this.discount.type.id == 'common' || this.discount.type.id == 'gift'){
            return {
                discount: {
                name: {required},
                type: {
                    id: {required}
                    },
                properties: {
                    subtotalFrom: {required}
                    }
                },
            }
        }else {
            return {
                discount: {
                name: {required},
                type: {
                    id: {required}
                    },
                },
            }
        }
    },
    props: {
        discountIndex: {
            type: Number,
        },
        isNew: {
            type: Boolean,
        }
    },
    computed: {
        ...mapGetters([
            'LABELS',
            'TYPES',
            'PRODUCT_LIST',
            'DISCOUNTS',
            'DESCRIPTION'
        ]),
        isValid(){
            let isValid;
               isValid = true;
            return isValid;
        },
        nameErrors() {
            const errors = []
            if (!this.$v.discount.name.$dirty) return errors
            if (!this.$v.discount.name.required) errors.push('Name is required.')
            return errors
        },
        typeErrors() {
            const errors = []
            if (!this.$v.discount.type.id.$dirty) return errors
            if (!this.$v.discount.type.id.required) errors.push('Type is required.')
            return errors
        },
        discountErrors() {
            const errors = []
            if (!this.$v.discount.properties.discount.$dirty) return errors
            if (this.discount.type.id == 'common' || this.discount.type.id == 'product'){
                if (!this.$v.discount.properties.discount.required) errors.push('Value is required.')
            }
            return errors
        },
        // subtotalErrors(){
        //     let error = []
        //     if (this.discount.type.id == 'common' || this.discount.type.id == 'gift'){
        //         console.log(this.$v)
        //         if (!this.$v.discount.properties.subtotalFrom.$dirty) return error
        //         if (!this.$v.discount.properties.subtotalFrom.required){
        //             error.push('Value is required.')
        //         }
        //     }
        //     return error
        // }
    },
    created(){
        if(this.discountIndex !== null){
            this.discount = this.DISCOUNTS[this.discountIndex];
            if (this.discount.products){
                for (let product of this.discount.products){
                    this.ADD_PRODUCT_TO_LIST(product);
                }
            }
            this.SET_CAT_DESCRIPTION(this.discount.properties.catDescription);
            this.SET_DESCRIPTION(this.discount.properties.productDescription);
            
        } else {
            this.discount.id = Date.now();
        }
    },
    methods: {
        ...mapActions([
            'SAVE_DISCOUNT',
            'ADD_PRODUCT_TO_LIST',
            'CLEAR_PRODUCT_LIST',
            'SET_DESCRIPTION',
            'SET_CAT_DESCRIPTION',
            'CLEAR_DESCRIPTION',
            'EDIT_ON_SERVER',
            'REMOVE_DISCOUNT',
        ]),
        receiveDates(dates){
            if (dates[0] > dates[1]){
                dates = dates.sort();
            }
            [this.discount.dateFrom, this.discount.dateTo] = dates;
        },
        subtotalFrom(value){
            this.discount.properties.subtotalFrom = value;
        },
        subtotalTo(value){
            this.discount.properties.subtotalTo = value;
        },
        saveDiscount(){
        
            if (this.$v.$invalid){
                this.$v.$touch()
                return
            }
            if (this.discount.type.id == 'common' || this.discount.type.id == 'gift'){
                if(this.discount.properties.subtotalTo && this.checkValues(this.discount.properties.subtotalFrom, this.discount.properties.subtotalTo)){
                    this.alert = true
                    this.alertContent = this.LABELS.subtotalAlert
                
                    return
                }
            }

            if(this.discount.type.id == 'payTake' && this.checkValues(this.discount.properties.itemsPay, this.discount.properties.itemsTake)){
                this.alert = true
                this.alertContent = this.LABELS.paytakeAlert
                return
            }
            if (!this.PRODUCT_LIST.length){
                if (this.discount.type.id == 'products' || this.discount.type.id == 'gift'){
                    this.alert = true
                    this.alertContent = this.LABELS.productListAlert
                    return
                }
            }
            if (this.isNew){
                this.SAVE_DISCOUNT(this.prepareDiscountObject(this.discount));
            } else {
                 this.SAVE_DISCOUNT(this.prepareDiscountObject(this.DISCOUNTS[this.discountIndex]));
            }
            this.CLEAR_PRODUCT_LIST();
            this.CLEAR_DESCRIPTION();
            // this.hideEditor();
        },
        prepareDiscountObject(discount){
            
            discount.type = this.TYPES.find(type => discount.type.id === type.id);
            discount.products = this.PRODUCT_LIST;
            if (this.DESCRIPTION){
                discount.properties.catDescription = this.DESCRIPTION.catDescription;
                discount.properties.productDescription = this.DESCRIPTION.productDescription;
            }
            return discount;
        },
        hideEditor(){
            this.$emit('hideEditor');
        },
        hideAlert() {
            this.alert = false;
        },
        checkValues(a, b){
            return a >= b
        }
    }
}
</script>

<style>
    .v-application input{
        border: none;
        box-shadow: none;
    }
    input[type="text"]{
        border: none;
    }
    input[readonly]{
        background-color: #fff;
    }
    .close-form{
        cursor: pointer;
    }
    .editor-elem{
        display: flex;
    }
    label{
        z-index: 1;
    }
    
</style>