<template>
          <div class="w-select-taxonomy">
              <v-row>
                  <v-col>
                        <v-select
                            :placeholder="LABELS.productListSearchCategory"
                            @change="addItemToList('cat')"
                            v-model="selectedCategoryId"
                            :items="categories"
                            item-text="name"
                            item-value="id">
                        </v-select>
                  </v-col>
              </v-row>
                <v-row>
                    <v-col>
                        <v-select
                            :placeholder="LABELS.productListSearchTag"
                            @change="addItemToList('tag')"
                            v-model="selectedTagId"
                            :items="tags"
                            item-text="name"
                            item-value="id">
                        </v-select>
                    </v-col>
                </v-row>

        </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'

export default {
    name: 'w-select-taxonomy',
    data(){
        return{
            selectedCategoryId: null,
            selectedTagId: null,
        }
    },
    computed: {
        ...mapGetters([
            'LABELS',
            'PRODUCT_LIST',
            'PRODUCTS',
        ]),
        categories(){
            let categories = this.PRODUCTS.filter(item => item.type == 'category');
            return categories;
        },
        tags(){
            let tags = this.PRODUCTS.filter(item => item.type == 'tag');
            return tags;
        }
    },
    methods: {
        ...mapActions([
            'ADD_PRODUCT_TO_LIST'
        ]),
        addItemToList(itemType){
            if (this.selectedCategoryId === null && this.selectedTagId === null){
                return;
            }
            let selectedItem;
            if (itemType == 'cat'){
                selectedItem = this.PRODUCTS.find(category => category.id === this.selectedCategoryId);
                selectedItem.itemType = 'category';
            } else if (itemType == 'tag'){
                selectedItem = this.PRODUCTS.find(tag => tag.id === this.selectedTagId);
                selectedItem.itemType = 'tag';
            }
            if (!this.PRODUCT_LIST.includes(selectedItem)){
                    this.ADD_PRODUCT_TO_LIST(selectedItem);
                    this.selectedCategoryId = null;
                    this.selectedTagId = null;
            }
        }
    }
}
</script>

