<template>
  <div class="w-discounts-list">
      <v-container>
          <v-simple-table>
                <thead>
                    <tr>
                        <th class="text-left">{{LABELS.discountListName}}</th>
                        <th class="text-left">{{LABELS.discountListType}}</th>
                        <th class="text-left hidden-xs-only">{{LABELS.discountListDateFrom}}</th>
                        <th class="text-left hidden-xs-only">{{LABELS.discountListDateTo}}</th>
                        <th class="text-left">{{LABELS.discountListStatus}}</th>
                        <th class="text-left">{{LABELS.discountListRemove}}</th>
                    </tr>
                </thead>
                    <tbody>
                        <w-discount-list-item 
                            v-for="(item, index) in DISCOUNTS"
                            :key="item.id"
                            :name="item.name"
                            :type="item.type"
                            :dateFrom="item.dateFrom"
                            :dateTo="item.dateTo"
                            @removeDiscount="removeDiscount(index)"
                            @editDiscount="editDiscount(index)"
                        />
                    </tbody>
        </v-simple-table>
      </v-container>
      <v-container d-flex class="justify-center">
          <v-btn class="centered-button"
                color="success"
                @click="createNewDiscount"
                >{{LABELS.newDiscountButton}}
            </v-btn>
      </v-container>
        <v-dialog fullscreen hide-overlay v-model="showEditor">
            <w-discount-editor 
                @hideEditor="showEditor=false"
                v-if="showEditor"
                :isNew="isNew"
                :discountIndex="discountIndex"/>
        </v-dialog>
  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
import wDiscountListItem from './w-discount-list-item'
import wDiscountEditor from './w-discount-editor'
export default {
    name: 'w-discounts-list',
    data(){
        return{
            discountIndex: null,
            showEditor: false,
        }
    },
    components: {
        wDiscountListItem,
         wDiscountEditor,
    },
    computed:{
        ...mapGetters([
            'DISCOUNTS',
            'LABELS'
        ]),
        isNew(){
            let isNew = false;
            if (this.discountIndex === null && this.discountIndex !== 0){
                isNew = true;
            } 
            return isNew;
        },
    },
    created(){
        this.GET_LABELS();
        this.GET_DISCOUNTS_FROM_API();
        this.GET_PRODUCTS_FROM_API();
    },
    methods: {
        ...mapActions([
            'REMOVE_DISCOUNT',
            'EDIT_DISCOUNT',
            'GET_DISCOUNTS_FROM_API',
            'GET_PRODUCTS_FROM_API',
            'GET_LABELS'
        ]),
        removeDiscount(index){
            let discountId = this.DISCOUNTS[index].id;
            console.log(discountId);
            let data = {
                index: index,
                id: discountId
            }
            this.REMOVE_DISCOUNT(data);
        },
        editDiscount(index){
            this.discountIndex = index;
            this.showEditor = true;
        },
        createNewDiscount(){
            this.discountIndex = null;
            this.showEditor = true;
        },
    }
}
</script>

<style scoped>
    .centered-button{
        justify-self: center;
    }
</style>